<template>
  <!-- Font class  -->
  <i v-if="type == 'fontClass'" :class="`iconfont ${name}`" :style="customSize" v-on="$listeners"></i>
  <!-- Symbol  -->
  <svg v-else class="icon svg-icon" aria-hidden="true" :style="customStyle" v-on="$listeners">
    <use :xlink:href="`#${name}`"></use>
  </svg>
</template>

<style src="@source/iconfont/iconfont.css"></style>
<script>
import '@source/iconfont/iconfont.js'
import customDark from './dark.js'

export default {
  props: {
    //  Font class  Symbol 
    // Font class font-size 
    // Symbol  
    type: {
      type: [String, Number],
      default: 'Symbol'
    },
    // 
    name: {
      type: String,
      default: '',
      required: true
    },
    // 
    size: {
      type: [String, Number],
      default: '18'
    },
    // Font class   -->  dark.js 
    dark: {
      type: String,
      default: ''
    }
  },
  computed: {
    // Font class 
    customSize() {
      const color = customDark[this.dark] || ''
      return {
        fontSize: `${this.size}px`,
        color: color ? color : this.dark
      }
    },
    // Symbol 
    customStyle() {
      const color = customDark[this.dark] || ''
      return {
        width: `${this.size}px`,
        height: `${this.size}px`,
        fill: 'currentColor',
        color: color ? color : this.dark
      }
    }
  }
}
</script>
