<template>
  <div :class="['my-checkbox', { disabled: disabled }]" @click="toggle">
    <icon-font
      :name="!isChecked ? offIcon : onIcon"
      v-bind="$attrs"
      :dark="isChecked ? 't_primary_dark_color' : 't_text_color_2'"
      class="flex-shrink-0"
      :size="iconSize"
    />
    <slot></slot>
  </div>
</template>

<script>
/**
 * radioBox 
 * @property {String Number} size px16
 * @property {String} offIcon 
 * @property {String} onIcon 
 * @property {Boolean} disabled 
 * @example <radio-box v-model="checked" disabled></radio-box>
 */
export default {
  props: {
    // 
    offIcon: {
      type: String,
      default: 'icon-weixuanzhong'
    },
    // 
    onIcon: {
      type: String,
      default: 'icon-xuanzhong'
    },
    // 
    value: {},
    // 
    disabled: {
      type: Boolean,
      default: false
    },
    iconSize: {
      type: [String, Number],
      default: 24
    }
  },
  inject: ['radioGroup'],
  data() {
    return {
      isChecked: this.value === this.radioGroup.value
    }
  },
  watch: {
    isChecked(newVal) {
      if (newVal) {
        this.radioGroup.$emit('input', this.value)
      }
    },
    'radioGroup.value'(newVal) {
      this.isChecked = this.value === newVal
    }
  },
  methods: {
    toggle() {
      if (!this.disabled) {
        this.isChecked = true
      }
    }
  }
}
</script>

<style>
.my-checkbox {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.my-checkbox.disabled {
  opacity: 0.3;
  cursor: not-allowed;
}
</style>
