<template>
  <div :class="['my-checkbox', { disabled: disabled }]" @click="radioFun">
    <icon-font :name="!isChecked ? offIcon : onIcon" v-bind="$attrs" class="flex-shrink-0"></icon-font>
    <slot></slot>
  </div>
</template>

<script>
/**
 * radioBox 
 * @property {String Number} size px16
 * @property {String} offIcon 
 * @property {String} onIcon 
 * @property {Boolean} disabled 
 * @example <radio-box v-model="checked" disabled></radio-box>
 */
export default {
  props: {
    // 
    offIcon: {
      type: String,
      default: 'icon-icon_account_xuanzhong2'
    },
    // 
    onIcon: {
      type: String,
      default: 'icon-icon_account_xuanzhong'
    },
    // 
    value: {
      type: Boolean,
      default: false
    },
    // 
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      isChecked: this.value
    }
  },
  watch: {
    isChecked(newVal) {
      this.$emit('input', newVal)
    },
    value(newVal) {
      this.isChecked = newVal
    }
  },
  methods: {
    radioFun() {
      return !this.disabled && (this.isChecked = true)
    }
  }
}
</script>

<style>
.my-checkbox {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.my-checkbox.disabled {
  opacity: 0.3;
  cursor: not-allowed;
}
</style>
